import React, { Component } from 'react';
import PropTypes from 'prop-types';
import update from 'react-addons-update';


import ListGroupItem from 'react-bootstrap/lib/ListGroupItem';

import LoaderAnimation from '../../components/loaderAnimation';

class DependencyLeaflet extends Component {
  static propTypes = {
    name: PropTypes.string.isRequired,
    version: PropTypes.string.isRequired
  }
  constructor(props){
    super(props);
    this.state = {
      loading: false,
      loadingError: false,
      pkgInformations: {},
    }
    this.loadPackageInformations(props);
  }
  async loadPackageInformations({ name, version }) {
    const superagent = await import('superagent');
    const pkg = await superagent
      .get(`https://skimdb.npmjs.com/registry/${name}`)
      .set('Accept', 'application/json')
      .set('User-Agent', 'Mozilla/5.0 (X11; Fedora; Linux x86_64; rv:57.0) Gecko/20100101 Firefox/57.0')
      .set('Pragma', 'no-cache')
      .set('Cache-Control', 'no-cache')
    console.log(name, pkg.versions[version].description);
    // if (!pkg.error) {
    //   this.setState(prevState =>
    //     update(prevState, {
    //       loadingError: { $set: true },
    //     }));
    // }
    // this.setState(prevState =>
    //   update(prevState, {
    //     loaded: { $set: true },
    //     pkgInformations: { $set: Object.assign({}, pkg.body.dependencies, pkg.body.devDependencies) },
    //   }));
  }
  header({ name, version }) {
    return (<h3>{name} <small>{version}</small></h3>);
  }
  render() {
    return (
      <ListGroupItem header={this.header(this.props)}>
        Cool !
      </ListGroupItem>
    );
  }
}

export default DependencyLeaflet;



